import React,{useState} from "react";
import { Link, Outlet } from "react-router-dom";

function Leads() {
  
  // const [isActive,setIsActive] = useState(false);
  const handleOnClick=()=>{
    // setIsActive(!isActive);
    // console.log(history);
      
  }

  // const getColor=(curr)=>{
  // if(history.location.pathname === curr){
  //   return ("grey");
  // }
  // }

 

  let mystyle = {
    body: {
      // fontFamily: "'Times New Roman', Times, serif",
      height:"100vh"
    },
    p:{
       fontSize:"20px",
       fontWeight:"bold",
    }
  };
  return (
    <>
      <div className="container-lg shadow p-3 mb-5 bg-body rounded mt-3" style={mystyle.body}>
        <h1 style={{ borderBottom: "1px solid black" }}>Leads</h1>
        <button
          type="button"
          class="btn btn-outline-secondary m-3 text-right btn shadow p-2 mb-0  rounded "
          onClick={handleOnClick}
          
          style={{
            width:"170px",
            maxWidth:"300px",
            minWidth:"100px",
            border: "2px solid #d2d9de",
            backgroundColor:  "rgb(210 217 222 / 22%)",
            // backgroundColor:getColor('/header/leads/Submitted')
            // backgroundColor:`${isActive ? "grey" : "rgb(210 217 222 / 22%)"}`
            // backgroundColor:`${bgcolor}`,
          }}
        >
          <Link to="/header/Leads/Submitted">
            <h6 className="text-dark "> SUBMITTED</h6>
            <p className="text-dark " style={mystyle.p}>0/4</p>
          </Link>
        </button>
        <button
          type="button"
          class="btn btn-outline-secondary m-3 text-right btn shadow p-2 mb-0  rounded"
          onClick={handleOnClick}
          style={{
            width:"170px",
            maxWidth:"300px",
            minWidth:"100px",
            border: "2px solid #d2d9de",
            backgroundColor: "rgb(210 217 222 / 22%)",
            // backgroundColor:getColor('/header/leads/Assigned')
            // backgroundColor:`${isActive ? "grey" : "rgb(210 217 222 / 22%)"}`
            // backgroundColor:`${bgcolor}`,
          }}
        >
          <Link to="/header/Leads/Assigned">
            <h6 className="text-dark">ASSIGNED</h6>
            <p className="lh-1 fs-3 text-dark " style={mystyle.p}>2/4</p>
          </Link>
        </button>
        <button
          type="button"
          class="btn btn-outline-secondary m-3 text-right btn shadow p-2 mb-0  rounded"
          onClick={handleOnClick}
          style={{
            width:"170px",
            maxWidth:"300px",
            minWidth:"100px",
            border: "2px solid #d2d9de",
            backgroundColor: "rgb(210 217 222 / 22%)",
            // backgroundColor:`${isActive ? "grey" : "rgb(210 217 222 / 22%)"}`
            // backgroundColor:`${bgcolor}`,
          }}
        >
          <Link to="/header/Leads/InProcess">
            <h6 className="text-dark">IN PROCESS</h6>
            <p className="text-dark " style={mystyle.p}>1/4</p>
          </Link>
        </button>
        <button
          type="button"
          class="btn btn-outline-secondary m-3 text-right btn shadow p-2 mb-0  rounded"
          onClick={handleOnClick}
          style={{
            width:"170px",
            maxWidth:"300px",
            minWidth:"100px",
            border: "2px solid #d2d9de",
            backgroundColor: "rgb(210 217 222 / 22%)",
            // backgroundColor:`${isActive ? "grey" : "rgb(210 217 222 / 22%)"}`
            // backgroundColor:`${bgcolor}`,
          }}
        >
          <Link to="/header/Leads/Disbursal">
            <h6 className="text-dark">DISBURSAL</h6>
            <p className="text-dark " style={mystyle.p}>1/4</p>
          </Link>
        </button>
        <button
          type="button"
          class="btn btn-outline-secondary flat m-3 text-right btn shadow p-2 mb-0  rounded"
          onClick={handleOnClick}
          style={{
            width:"170px",
            maxWidth:"300px",
            minWidth:"100px",
            border: "2px solid #d2d9de",
            backgroundColor: "rgb(210 217 222 / 22%)",
            // backgroundColor:`${isActive ? "grey" : "rgb(210 217 222 / 22%)"}`
            // backgroundColor:`${bgcolor}`,
          }}
        >
          <Link to="/header/Leads/Rejected">
            <h6 className="text-dark">REJECTED</h6>
            <p className="text-dark " style={mystyle.p}>0/4</p>
          </Link>
        </button>

        <Outlet />
      </div>
    </>
  );
}

export default Leads;
