import React, {useState} from 'react'
import { ProSidebar, Menu, MenuItem, SubMenu } from 'react-pro-sidebar';
import { Link } from 'react-router-dom';
import 'react-pro-sidebar/dist/css/styles.css';
import Popover from 'react-bootstrap/Popover';
import CloseIcon from '@mui/icons-material/Close';
import ifl from '../ifl.png'
import DashboardIcon from '@mui/icons-material/Dashboard';
import AssessmentIcon from '@mui/icons-material/Assessment';
// import Header from '../Header'
// import Footer from '../Footer'
function SideBar({setShow}) {
 
  return (
    <div >
      <Popover
        id="popover-basic"
        style={{ maxWidth: 2000, margin: "30px", overflow: "auto" ,maxHeight:2000}}
      >
       <ProSidebar style={{fontFamily:"'Times New Roman', Times, serif"}}>
       
       
       <span style={{display:"flex"}}>
       <img className="mt-3 mb-0" src={ifl} alt=""  style={{width:"100px",heigth:"100px"}}/><h3 className='ml-3 mt-4 mb-0'> LOS</h3>
       <CloseIcon className='icon ml-5'  onClick={()=>setShow(false)} style={{float:"right"}}/>
       </span>
        <Menu iconShape="square">
        <SubMenu>
          <MenuItem > 
          <Link to="/header/Dashboard">
          <DashboardIcon/> Dashboard
          </Link>
          </MenuItem>
          
            <MenuItem>
            <Link to="/header/App_Header">
            <DashboardIcon/> Applications
            </Link>
            </MenuItem>

            <MenuItem>
            <Link to="/header/Leads">
            <AssessmentIcon/> Lead Reports
            </Link>
           </MenuItem>

            <MenuItem>
            <Link to="/header/Reports">
            <AssessmentIcon/> Reports
            </Link>
            </MenuItem>
            </SubMenu>
        </Menu>
      </ProSidebar>
      </Popover>
    </div>
  )
}

export default SideBar
