import React from 'react'
import {Link} from 'react-router-dom'
import { useNavigate } from 'react-router-dom'
import { Outlet } from 'react-router-dom';
import Row from 'react-bootstrap/esm/Row';
import Col from 'react-bootstrap/esm/Col';
import Button from 'react-bootstrap/esm/Button';
function Reports() {
  let navigate = useNavigate();
    let mystyle={
        body:{
            // fontFamily:"'Times New Roman', Times, serif",
            height:"100vh"
        },
        btn:{
          width:"24%",
          // float: "right",
        }
    }
    
  return (
    <div className='container-lg shadow p-3 mb-5 bg-body rounded mt-3 mb-5' style={mystyle.body}>
      <h2 className='p-2 ' style={{borderBottom:"1px solid black"}}>Reports</h2>
      <div style={{float:"right"}}>
        <Button className='btn shadow p-2 mb-5 ml-3 bg-info  rounded' variant="primary" type="submit" style={{width:"100px",border:"1px solid grey"}} >Generate</Button>
        <Button className='btn shadow p-2 mb-5 ml-3 bg-secondary  rounded' variant="primary" type="close" style={{width:"100px" ,border:"1px solid grey"}} >Clear</Button>
        </div>
      <div className='container-lg ml-3 mt-3'  >
      <Row>
      <Col>
        <div  >
        <input class="form-check-input"  onClick={()=>{navigate("/header/Reports/Gold_rate")}} type="radio" name="flexRadioDefault" id="flexRadioDefault1"/>
        <h5> Gold Rate</h5> 
        
        </div>
        </Col>
        <Col>
        <div >
       
      
        <input  class="form-check-input " type="radio" onClick={()=>{navigate("/header/Reports/Application")}} name="flexRadioDefault" id="flexRadioDefault2" />
        <h5>Application</h5>
        </div>
        </Col>
        </Row>
        </div>
         
          
        
      <Outlet/>
      <div className='container-lg mb-3' style={mystyle.btn} >
        <Button className='btn shadow p-2 mb-3 ml-3 bg-info  rounded' variant="primary" type="submit" style={{width:"100px",border:"1px solid grey"}} >Generate</Button>
        <Button className='btn shadow p-2 mb-3 ml-3 bg-secondary  rounded' variant="primary" type="close" style={{width:"100px" ,border:"1px solid grey"}} >Clear</Button>
        </div>
        
    </div>
   
  )
}

export default Reports
