import React, {useState} from 'react'
import ifl from './ifl.png'
import DehazeIcon from '@mui/icons-material/Dehaze';
import AccountBalanceIcon from '@mui/icons-material/AccountBalance';
import PersonIcon from '@mui/icons-material/Person';
import ScheduleIcon from '@mui/icons-material/Schedule';
import CalendarMonthIcon from '@mui/icons-material/CalendarMonth'
import Dropdown from 'react-bootstrap/Dropdown';
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css';

import './Header.css';
import { Outlet, useNavigate } from 'react-router-dom';
import SideBar from './los/SideBar';
// import {Link} from 'react-router-dom';



function Header(props) {

  let navigate = useNavigate();
  const [show,setShow] = useState(false);

  const onClickIcon=()=>{
    //  navigate("/Los");
    console.log("clicked");
    setShow(true);

  }

  const handleOnClick=()=>{
       confirmAlert({
        title:'Confirmation ',
        message:"Are You sure to Log Out?",

        buttons:[
          {
            label: 'Yes',
            onClick:()=>navigate("/"),
          },
          {
            label: 'No',
            onClick:()=>navigate("/header"),
          }
        ]
       })
  }
  
  const handleOnSSO=()=>{
    navigate("/header");
  }

  const current = new Date();
  const date = `${current.getDate()}-${current.getMonth()+1}-${current.getFullYear()}`;
  const time = `${current.getHours()}.${current.getMinutes()}.${current.getSeconds()}`;
  let mystyle={
    body:{
        // fontFamily:" 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif",
        borderBottom:"1px solid black"
      },
    header:{
      backgroundColor:"rgb(230, 242, 252)",
    
    },
    logo:{
        height: "70px",
        width: "150px",
        marginTop: "20px",
    },
    person:{
        height: "35px",
        weight: "50px"
    }

  }
  return (

    

    <div className="shadow-sm mb-2 bg-body rounded" style={mystyle.body}>    
      <div className=" row hstack gap-3 overflow-visible bg-rgb(230, 242, 252)" style={mystyle.header}>
         <div className="col-7" style={mystyle.left_part} >
              <DehazeIcon onClick={onClickIcon} className='ml-3' />
              {show && <SideBar setShow={setShow}/>}
              <img src={ifl} style={mystyle.logo}  alt="image not loaded..." />
         </div>
         
         <div className=" gap-2  mt-3 ml-5 ms-auto" style={mystyle.date}>
             <p className='mb-0'>
             <CalendarMonthIcon/>{date}
            </p>
            <p className='mt-0'>
            <ScheduleIcon/>{time}
            </p>
         </div>
         <div className="account ml-5 ms-auto" style={mystyle.account}>
         <span>
         <AccountBalanceIcon />
          ADHYAPAK NAGAR
         </span>
         </div>
         {/* <div className="person  float-end ml-5 " style={mystyle.person}>
            <span>
            <PersonIcon/>maker
            </span>
            </div> */}

            <Dropdown>
        <Dropdown.Toggle className='ml-3' variant="info" style={mystyle.person} id="dropdown-basic">
        <div className="person  float-end  " style={mystyle.person}>
            <span>
            <PersonIcon/>{props.name}
            </span>
            </div>
        </Dropdown.Toggle>
        <Dropdown.Menu>
            <Dropdown.Item to="/" onClick={handleOnClick}>
            Log Out
            </Dropdown.Item>
            <Dropdown.Item to="#/action-2" onClick={handleOnSSO}>Exit To SSO</Dropdown.Item>
        </Dropdown.Menu>
        </Dropdown>        
      </div>
      <Outlet/>
    </div>
   
  )
}

export default Header
