import React, {useEffect, useState } from "react";
import ifl from "./ifl_lms/ifl.png";
import "./Login.css";
import PersonIcon from "@mui/icons-material/Person";
import PasswordIcon from "@mui/icons-material/Password";
import { Link, Navigate, Outlet } from "react-router-dom" ;
import Form from 'react-bootstrap/Form';
import { useNavigate } from "react-router-dom";
import {toast} from "react-toastify";
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer } from "react-toastify";
// import { toBePartiallyChecked } from "@testing-library/jest-dom/dist/matchers";
import { useKeyboard } from "react-keyboard-event";

function Login(props) {
  let navigate = useNavigate(); 
   
  let user = "rakesh";
  const [username,setUserName] = useState('');
  const [inputPassword,setInputPassword] = useState('');

  const handleSubmit=(e)=>{
     e.preventDefault();
    //  console.log("submitted");
    //  alert(`The name you entered is ${username}`);
    let user = `${props.name}`;
     if(!username || !inputPassword){
      console.log("usernmae not presrnt");
      toast("User Name or Password should not be empty");
     }
     else if(username !==   user || inputPassword !== "Test@123"){
      toast("Incorrect user name or password");
     }
     else if(username === user && inputPassword === "Test@123"){
      navigate("/header");
     }
  }
//useEffect hook is used to add key event listener i.e by pressing enter the submit buttn will work

  // useEffect(() => {
  //    const listener = event => {

  //     if (event.code === "Enter" || event.code === "NumpadEnter") {
        
  //       console.log("Enter key was pressed. Run your function.");
  //       event.preventDefault();
  //       if(!username || !inputPassword){
  //         console.log("usernmae not presrnt");
  //         toast("User Name or Password should not be empty");
  //        }
  //        else if(username !== "maker" || inputPassword !== "Delhi@123"){
  //         toast("Incorrect user name or password");
  //        }
  //        else if(username === "maker" && inputPassword === "Delhi@123"){
  //         navigate("/main");
  //        }
  //       // navigate("/main");
      
  //     }
  //   };
  //   document.addEventListener("keydown", listener);
  //   return () => {
  //     document.removeEventListener("keydown", listener);
  //   };

  // }, []);

  //KeyDownHandler is also used for keyboard listener events
  function keyDownHandler(event: React.KeyboardEvent<HTMLInputElement>) {
    if (event.code === "Enter") {
      if (!username || !inputPassword) {
        console.log("usernmae not presrnt");
        toast("User Name or Password should not be empty");
      }
      else if (username !== user || inputPassword !== "Test@123") {
        toast("Incorrect user name or password");
      }
      else if (username === user && inputPassword === "Test@123") {
        navigate("/header");
      }
      // alert(`You have typed "${username}"`);
    }
  }
  let mystyle = {
    container: {
      width: "40vw",
      border: "1px solid grey",
      marginTop: "90px",
    },
    image: {
      height: "70px",
      width: "150px",
      marginTop: "20px",
    },

    input: {
      marginBottom: "0px",
      backgroundColor: "white",
      marginLeft: "25px",
      maxWidth: "300px",
      minWidth: "50px",
    },
    hr: {
      boxShadow: "0 20px 20px -20px #333",
    },
    Button: {
      width: "100px",
      border: "1px solid black",
    },
    forget: {
      color: "green",
    },
  };
  return (
    <div>
      <form onSubmit={handleSubmit}>
      <div className="container mb-5 " style={mystyle.container}>
        <div className="text-center">
          <img src={ifl} style={mystyle.image} alt="" />
          <hr style={mystyle.hr} />
        </div>

        <p className="mx-3 mb-0">
          <PersonIcon /> User Name
        </p>
        <div className="input-group mb-3  ">
          <input
            type="text"
            className="form-control mx-3  "
            style={mystyle.input}
            value={username}
            onChange={(e)=>{setUserName(e.target.value)}}
            name="username"
            onKeyDown={keyDownHandler}
            placeholder="Username"
            aria-label="Username"
          />
        </div>
        <p className="mx-3 mb-0" style={mystyle.p}>
          <PasswordIcon /> Password
        </p>
        <div className="col-sm-10">
          <input
            type="password"
            className="form-control mx-2"
            style={mystyle.input}
            value={inputPassword}
            onChange={(e) => {
              setInputPassword(e.target.value);
            }}
            onKeyDown={keyDownHandler}
            id="inputPassword"
            placeholder="Enter Your Password"
          />
        </div>
        <div className="text-center mt-3">
        
        <button
            type="submit"
            className="btn btn-success  text-center "
            style={mystyle.Button}
            // onClick={()=>{navigate("/main")}}
          >
            Submit
          </button>
          
         
          <Link to="/Forget_Password">
            <p style={mystyle.forget}>forgot password?</p>
          </Link>
        </div>
      </div>


      </form>
      <Outlet/>    
      <ToastContainer />  
     
    </div>
  );
}

export default Login;
