import React from 'react'

function Lms_Dashboard() {
  return (
    <div className='container-lg shadow p-3 mb-5 bg-body rounded mt-3' style={{height:"100vh"}}>
      <h2 style={{borderBottom:"1px solid black"}}>My Dashboard</h2>
      <div style={{justifyContent:"space-between"}}>
      <div className="container-lg m-3 p-2 "style={{width:"40%",height:"40px",backgroundColor:"lightgrey",float:"left"}}>
          <h4>Recent Comment</h4> 
      </div>
      <div className="container-lg m-3 p-2"style={{width:"40%",height:"40px",backgroundColor:"lightgrey",float:"right"}}>
       <h4>Recent Activity</h4> 
        </div>
      </div>
     

    </div>
  )
}

export default Lms_Dashboard
