import React from "react";
import Popover from "react-bootstrap/Popover";
import Table from "react-bootstrap/esm/Table";
import DeleteIcon from "@mui/icons-material/Delete";
import Button from "react-bootstrap/esm/Button";
import CloseIcon from '@mui/icons-material/Close';
import './LoanDetails.css';

function Loan_Details_header({ setOpenLoginDetails }) {
  let mystyle = {
    table: {
      tableLayout: "fixed",
      width: "100%",
      marginBottom: "110px",
      overflow: "auto",
      border: "2px solid grey",
    },
  };
  return (
    <div>
      <Popover
        id="popover-basic"
        style={{ maxWidth: 2000, margin: "100px", overflow: "scroll" }}
      >
        <div className="container">
          <div className="container-lg ">
          <CloseIcon className='icon'  onClick={()=>setOpenLoginDetails(false)} style={{float:"right"}}/>
            <ul
              className="nav"
              style={{
                fontWeight: "5px",
                display: "flex",
                flexDirection: "row",
                overflow: "auto",
              }}
            >
              <li className="nav-item m-3  ml-0">
                <p>LOAN DETAILS</p>
              </li>
              <li className="nav-item m-3 ">
                <p className="fs-3">APPLICANT</p>
              </li>
              <li className="nav-item m-3 ">
                <p>REVIEW</p>
              </li>
              <li className="nav-item m-3 ">
                <p>BANK DETAILS</p>
              </li>
              <li className="nav-item m-3 ">
                <p>NOMINEE DETAILS</p>
              </li>
              <li className="nav-item m-3 w-4">
                <p className="fs-6">DOCUMENT</p>
              </li>
              <li className="nav-item m-3 w-4">
                <p className="fs-6">SANCTION</p>
              </li>
              <li className="nav-item m-3 w-4">
                <p className="fs-6">ESIGN</p>
              </li>
            </ul>
            <div className="container-lg">
              <h2 className="p-3" style={{ borderBottom: "1px solid black" }}>
                Loan Details
              </h2>
              <div className="container-lg">
                <ul
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                  }}
                >
                  <li className="m-3">
                    <p className="mb-0">Gross Weight</p>
                    <input type="number" name="Gross Weight" id="" />
                  </li>
                  <li classname="m-3">
                    <p className="mb-0">Deduction</p>
                    <input type="number" name="Gross Weight" id="" />
                  </li>
                  <li className="m-3">
                    <p className="mb-0">Total net Weight(gm)</p>
                    <input type="number" name="Gross Weight" id="" />
                  </li>
                  <li className="m-3">
                    <p className="mb-0">Total Count</p>
                    <input type="number" name="Gross Weight" id="" />
                  </li>
                  <li className="m-3">
                    <p className="mb-0">Total Tare Weight</p>
                    <input type="number" name="Gross Weight" id="" />
                  </li>
                </ul>
                <Table
                  className="table-responsive shadow-sm p-3 mt-2 mb-4  bg-light rounded "
                  striped
                  bordered
                  hover
                  style={mystyle.table}
                >
                  <thead>
                    <tr style={{ backgroundColor: "rgb(210 217 222 / 22%)" }}>
                      <th style={{ width: "0%" }}>Particulars</th>
                      <th style={{ width: "10%" }}>Finesse code</th>
                      <th style={{ width: "10%" }}>Gold Rate(per gm)</th>
                      <th style={{ width: "10%" }}>Gross Weight(gm)</th>
                      <th style={{ width: "100px" }}>Deduction</th>
                      <th style={{ width: "50px" }}>Net Weight</th>
                      <th style={{ width: "50px" }}>Count</th>
                      <th style={{ width: "50px" }}>Packet No.</th>
                      <th style={{ width: "5%" }}></th>
                    </tr>
                    <tr>
                      <td>
                        <select
                          style={{ height: "30px", width: "100px" }}
                          class="form-select"
                          aria-label="Default select example"
                        >
                          <option selected>RING</option>
                          <option value="1">BABY BANGLE</option>
                          <option value="2">BANGLES</option>
                          <option value="3">BLACK BEADS CHAIN</option>
                          <option value="4">BRACELET</option>
                          <option value="5">CHAIN</option>
                          <option value="6">DROPS</option>
                          <option value="7">EARRING</option>
                          <option value="8">JUMKIES</option>
                          <option value="9">LOCKET</option>
                          <option value="10">MATTIES</option>
                          <option value="11">NECKLACE</option>
                          <option value="12">RING</option>
                          <option value="13">STUDDROPS</option>
                          <option value="14">STUDS</option>
                          <option value="15">VADDANAM</option>
                          <option value="16">BROKEN BABY BANGLES</option>
                          <option value="17">BROKEN BANGLES</option>
                          <option value="18">BROKEN BBCHAIN</option>
                          <option value="19">BROKEN BRACELET</option>
                          <option value="20">BROKEN CHAIN</option>
                          <option value="21">BROKEN DROPS</option>
                          <option value="22">BROKEN EARRINGS</option>
                          <option value="23">BROKEN JUMKIES</option>
                          <option value="24">BROKEN LOCKET</option>
                          <option value="25">BROKEN MATTIES</option>
                          <option value="26">BROKEN NECKLACE</option>
                          <option value="27">BROKEN RING</option>
                          <option value="28">BROKEN STUDDROPS</option>
                          <option value="29">BROKEN STUDS</option>
                          <option value="30">BROKEN VADDANAM</option>
                          <option value="31">TIKKA</option>
                          <option value="32">CHAIN WITH LOCKET</option>
                          <option value="33">MIS.</option>
                          <option value="34">BROKEN TIKKA</option>
                          <option value="35">BROKEN CHAIN WITH LOCKET</option>
                          <option value="36">BROKEN MIS.</option>
                        </select>
                      </td>
                      <td>
                        <select
                          style={{ height: "30px" }}
                          class="form-select"
                          aria-label="Default select example"
                        >
                          <option selected>18k</option>
                          <option value="1">19k</option>
                          <option value="2">20k</option>
                          <option value="3">21k</option>
                          <option value="4">22k</option>
                        </select>
                      </td>
                      <td>3438.86</td>
                      <td>
                        <input
                          style={{ width: "100px" }}
                          type="text"
                          name=""
                          id=""
                        />
                      </td>

                      <td>
                        <input
                          style={{ width: "100px" }}
                          type="text"
                          name=""
                          id=""
                        />
                      </td>
                      <td>10</td>
                      <td>
                        <input
                          style={{ width: "100px" }}
                          type="text"
                          name=""
                          id=""
                        />
                      </td>
                      <td>
                        <input
                          style={{ width: "100px" }}
                          type="text"
                          name=""
                          id=""
                        />
                      </td>
                      <td>
                        <DeleteIcon />
                      </td>
                    </tr>
                  </thead>
                </Table>
                <div
                  classname="mt-3"
                  style={{
                    width: "500px",
                    display: "flex",
                    flexDirection: "row",
                  }}
                >
                  <Button
                    className="btn shadow p-2 mb-3 ml-3 bg-info  rounded"
                    variant="primary"
                    type="submit"
                    style={{
                      width: "30%",
                      border: "1px solid grey",
                      height: "50px",
                    }}
                  >
                    ADD
                  </Button>
                  <Button
                    className="btn shadow p-2 mb-3 ml-3 bg-secondary  rounded"
                    variant="primary"
                    type="close"
                    style={{
                      width: "30%",
                      border: "1px solid grey",
                      height: "50px",
                    }}
                  >
                    CALCULATE
                  </Button>
                </div>
                <ul
                  style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                  }}
                >
                  <li className="m-3 ml-0 " style={{ width: "100px" }}>
                    <p className="mb-0">Ornament Value</p>
                    <input
                      style={{ width: "130px" }}
                      type="number"
                      name="Gross Weight"
                      id=""
                    />
                  </li>
                  <li classname="m-3" style={{ width: "130px" }}>
                    <p className="mb-0">Actual Loan Amount</p>
                    <input
                      style={{ width: "130px" }}
                      type="number"
                      name="Gross Weight"
                      id=""
                    />
                  </li>
                  <li className="m-3" style={{ width: "100px" }}>
                    <p className="mb-0">Loan Scheme</p>
                    <input
                      style={{ width: "130px" }}
                      type="number"
                      name="Gross Weight"
                      id=""
                    />
                  </li>
                  <li classname="m-3" style={{ width: "100px" }}>
                    <p className="mb-0">Monthly Interest</p>
                    <input
                      style={{ width: "130px" }}
                      type="number"
                      name="Gross Weight"
                      id=""
                    />
                  </li>
                  <li className="m-3" style={{ width: "130px" }}>
                    <p className="mb-0">Tenure (in Months)*</p>
                    <input
                      style={{ width: "130px" }}
                      type="number"
                      name="Gross Weight"
                      id=""
                    />
                  </li>
                  <li className="m-3" style={{ width: "100px" }}>
                    <p className="mb-0">LTV</p>
                    <input
                      style={{ width: "130px" }}
                      type="number"
                      name="Gross Weight"
                      id=""
                    />
                  </li>
                </ul>

                <ul
                  style={{
                    display: "flex",
                    // justifyContent: "space-between",
                    alignItems: "center",
                  }}
                >
                  <li className="ml-5">
                    <p className="mb-0">PAN NO.</p>
                    <input type="text" />
                  </li>
                  <li className="ml-5">
                    <p></p>
                    <Button
                      className="btn mt-2 shadow p-2 mb-3 ml-3 bg-secondary  rounded"
                      variant="primary"
                      style={{
                        width: "150px",
                        border: "1px solid grey",
                        height: "40px",
                      }}
                    >
                      Verify
                    </Button>
                  </li>
                  <li className="ml-5">
                    <p className="mb-0"> APPLICANT NAME</p>
                    <input type="text" />
                  </li>
                </ul>
                <div className="container-lg">
                  <Table no striped bordered hover>
                    <thead>
                      <tr>
                        <th>Days</th>
                        <th>Interest</th>
                      </tr>
                      <tr>
                        <td>0-30</td>
                        <td>1% PM (12% PA)</td>
                      </tr>
                      <tr>
                        <td>31-60</td>
                        <td>1.25% PM (15% PA)</td>
                      </tr>
                      <tr>
                        <td>61-90</td>
                        <td>1.5% PM (18% PA)</td>
                      </tr>
                      <tr>
                        <td>91-120</td>
                        <td>2% PM (24% PA)</td>
                      </tr>
                    </thead>
                  </Table>
                  <div
                    className="container-lg mb-3"
                    style={{
                      display: "inline-flex",
                      flexDirection: " row-reverse",
                    }}
                  >
                    <Button
                      className="btn shadow p-2 mb-3 ml-3 bg-info  rounded"
                      variant="primary"
                      type="submit"
                      style={{ width: "100px", border: "1px solid grey" }}
                    >
                      Next
                    </Button>
                    <Button
                      className="btn shadow p-2 mb-3 ml-3 bg-secondary  rounded"
                      variant="primary"
                      onClick={()=>setOpenLoginDetails(false)}
                      type="close"
                      style={{ width: "100px", border: "1px solid grey" }}
                    >
                      Back
                    </Button>
                  </div>
                </div>
              </div>
            </div>

            {/* <button onClick={() => setOpenLoginDetails(false)}>close</button> */}
          </div>
        </div>
      </Popover>
    </div>
  );
}

export default Loan_Details_header;
