import React from 'react'
import Table from 'react-bootstrap/Table'
import SearchIcon from '@mui/icons-material/Search';
function dashboard() {
  
  let mystyle={
    body:{
      // fontFamily:"'Times New Roman', Times, serif",
      height:"100vh"
    },
    table:{
        tableLayout:"fixed",
        width:"100%",
        marginBottom:"110px",
        overflow: "auto",
        border:"2px solid grey",
    // overflow: "auto",
        
    },
    th:{
      width:"310px",
      maxWidth:"300px",
      minWidth:"100px",
    }

  }
  return(
    <div className="container-sm" style={mystyle.body}>
      <h2 className='mb-5 p-2 mt-3 shadow p-3  bg-body rounded'  style={{borderBottom:"1px solid grey",color:"#8383b6"}}>My Dashboard</h2>
      <div className="container-lg shadow-sm p-3 mb-5 bg-body rounded">
      <form className="d-flex" role="search">
        <input className="form-control me-2 mb-2 mr-2" type="search" placeholder= "Search" aria-label="Search" />
        <button className="btn btn-success mb-2" type="submit" variant="success">Search</button>
        
      </form>

        <Table className='table-responsive shadow-sm p-3 mt-3  mb-4  bg-body rounded   shadow-sm p-3  bg-body rounded' striped bordered hover  style={mystyle.table}>
            
            <thead>
                <tr style={{backgroundColor:"rgb(210 217 222 / 22%)",}} >
                    <th style={mystyle.th} >
                    Loan Request Num
                    </th>
                    <th style={mystyle.th}>
                    Proposed Loan Amt
                    </th>
                    <th style={mystyle.th}>
                    Created Date
                   </th>
                   <th style={mystyle.th}>
                   Select Agent
                   </th>
                   <th style={mystyle.th}>
                   Actions
                   </th>
                </tr>
            </thead>
        </Table>
      </div>
      
      
    </div>
  )
}

export default dashboard
