import React, { useState } from 'react'
import los from './los.png'
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import lms from './lms.png'
import Row  from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Header from './Header';
import Footer from './Footer';
import './Main.css'
import SideBar from './los/SideBar';
import { Outlet, useNavigate } from 'react-router-dom';
import { handleBreakpoints } from '@mui/system';

function Main(props) {

   
  const [openLos,setOpenLos] = useState(false);
  let navigate= useNavigate();

  const handleOnLos=()=>{
    navigate("/header/Dashboard");
    setOpenLos(true);
  }

  const handleOnLms=()=>{
    navigate("/header/Lms_Dashboard")
  }

  let mystyle={
   body:{
    //  fontFamily:" 'Times New Roman', Times, serif",
   },
   cards:{
     display:"flex",
     flexDirection:"row",
     alignItems:"center",
     justifyContent:"center",
   },
   img:{
    alignSelf:"center",
    marginTop:"20px",
    height:"100px",
    width:"100px"
   },
   h4:{
    fontWeight:"bold"
   }
   
  }
  return (
   <>
    {/* <Header/> */}

    <div style={mystyle.body}>
      <div className="text-center mt-2 ">
        <h3 style={mystyle.h4}>Welcome <i>{props.name}</i> to IFL LMS</h3>
      </div>
      <div className="cards text-center" style={mystyle.cards}>
      <Row >
        <Col>
        <Card className='shadow p-3 mb-5 bg-body rounded text-center ml-4 mt-4 ' style={{ height:"20rem", width: '23rem',backgroundColor:'rgb(230, 242, 252)',border:"2px solid grey" }}>
            
        <Card.Img variant="top" style={mystyle.img} src={los} />
        <Card.Body>
        <Card.Title>
            <h3>LOS</h3>
        </Card.Title>
        <Card.Text>
        For Faster Customer Acquisition
        </Card.Text>
        <Button className='btn shadow p-2 mb-5 bg-info rounded' variant="primary" onClick={handleOnLos}>Open Module</Button>
        </Card.Body>
        </Card>
        </Col>

        <Col >

        <Card className='shadow p-3 mb-5 bg-body rounded text-center ml-4 mt-4 ' style={{ height:"20rem",width: '23rem',backgroundColor:'rgb(230, 242, 252)',border:"2px solid grey" }}>
        <Card.Img variant="top" style={mystyle.img} src={lms} />
        <Card.Body>
        <Card.Title>
            <h3>LMS</h3>
        </Card.Title>
        <Card.Text>
        Next Generation Loan Servicing
        </Card.Text>
        <Button className='btn shadow p-2 mb-5 bg-info  rounded' variant="primary" onClick={handleOnLms} >Open Module</Button>
        </Card.Body>
        </Card>
        </Col>
      </Row>
      </div>
      <Outlet/>
    </div>
    {/* <Footer/> */}
    </>
  )
}

export default Main
