import React from 'react'
import Popover from "react-bootstrap/Popover";
import Table from "react-bootstrap/esm/Table";
import DeleteIcon from "@mui/icons-material/Delete";
import Button from "react-bootstrap/esm/Button";
import CloseIcon from '@mui/icons-material/Close';

function DisbursalDetails({setOpenDisbursalDetails}) {
  return (
    <div>
      <div>
          <Popover
            id="popover-basic"
            style={{ maxWidth: 2000, margin: "100px",  }}
          >
            <div className="container">
              <div className="container-lg ">
              <CloseIcon className='icon'  onClick={()=>setOpenDisbursalDetails(false)} style={{float:"right"}}/>
                <ul
                  className="nav"
                  style={{
                    fontWeight: "5px",
                    display: "flex",
                    flexDirection: "row",
                    overflow: "auto",
                  }}
                >
                  <li className="nav-item m-3  ml-0">
                    <p>LOAN DETAILS</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p className="fs-3">APPLICANT</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p>REVIEW</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p>BANK DETAILS</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p>NOMINEE DETAILS</p>
                  </li>
                  <li className="nav-item m-3 w-4">
                    <p className="fs-6">DOCUMENT</p>
                  </li>
                  <li className="nav-item m-3 w-4">
                    <p className="fs-6">SANCTION</p>
                  </li>
                  <li className="nav-item m-3 w-4">
                    <p className="fs-6">ESIGN</p>
                  </li>
                </ul>
                <div className="container-lg">
                  <h2 className="p-3" style={{ borderBottom: "1px solid black" }}>
                    Disbursal Details
                  </h2>
                    <div className="container-lg">
                        <div  style={{float:"right"}}>
                        <p className='mb-0'>Disbursal Date</p>
                        <input  type="date" name="" id="" />
                        </div>
                       <div className='p-0 mb-3' style={{borderBottom:"1px solid black"}}>
                    <ul
                  className="nav"
                  style={{
                    fontWeight: "5px",
                    display: "flex",
                    flexDirection: "row",
                    overflow: "auto",
                  }}
                >
                  <li className="nav-item ml-3 mt-3  ml-0">
                    <p>Disbursal Amount</p>
                  </li>
                  <li className="nav-item ml-3 mt-3 ">
                    <p className="fs-3">Adjustment</p>
                  </li>
                  <li className="nav-item ml-3 mt-3 ">
                    <p>Adjusted Account/Amount</p>
                  </li>
                  <li className="nav-item ml-3 mt-3 ">
                    <p>Net Amount Payable</p>
                  </li>
                  <li className="nav-item ml-3 mt-3 ">
                    <p>Cash</p>
                  </li>
                  <li className="nav-item ml-3 mt-3 w-4">
                    <p className="fs-6">Bank</p>
                  </li>
                  
                 
                </ul>
                </div> 
                  

                      

                      <div
                        className="container-lg mb-3"
                        style={{
                          display: "inline-flex",
                          flexDirection: " row-reverse",
                        }}
                      >
                        <Button
                          className="btn shadow p-2 mb-3 ml-3 bg-info  rounded"
                          variant="primary"
                          type="submit"
                          style={{ width: "100px", border: "1px solid grey" }}
                        >
                          Next
                        </Button>
                        <Button
                          className="btn shadow p-2 mb-3 ml-3 bg-secondary  rounded"
                          variant="primary"
                          onClick={()=>setOpenDisbursalDetails(false)}
                          type="close"
                          style={{ width: "100px", border: "1px solid grey" }}
                        >
                          Back
                        </Button>
                      </div>
                    </div>
                  
                </div>
    
                {/* <button onClick={() => setOpenLoginDetails(false)}>close</button> */}
              </div>
            </div>
          </Popover>
        </div>
    </div>
  )
}

export default DisbursalDetails
