import React, { useState } from 'react'
import { Link,Outlet } from 'react-router-dom'


function App_Header() {

  //  const [backgroundColor,setBackgroundColor] = useState("");

  // const handleOnClick=()=>{
  //   setBackgroundColor("grey")
  // }
    let mystyle={
        body:{
            // fontFamily:"'Times New Roman', Times, serif",
            height:"100vh"
        }

    }
  return (
    <div style={mystyle.body}>
      <div className="container-lg mb-3  mt-3 ml-5    bg-body rounded" >
      <ul className="nav" style={{fontWeight:"5px",fontSize:"20px",display:"flex",flexDirection:"row",overflow: "auto"}}>
        <li className="nav-item">
          <Link className="nav-link text-dark " aria-current="page" to="/header/App_Header/In_process">IN PROCESS
          </Link>
            
        </li>
        <li className="nav-item">
        <Link className="nav-link text-dark " to="/header/App_Header/Submitted" > SUBMITTED
        </Link>
        </li>
        <li className="nav-item">
        <Link  className="nav-link text-dark" to="/header/App_Header/Disbursal"> DISBURSAL
        </Link>
        </li>
        <li className="nav-item">
        <Link className="nav-link text-dark" to="/header/App_Header/Completed"> COMPLETED
        </Link>
        </li>
        
        <li className="nav-item">
        <Link className="nav-link text-dark" to="/header/App_Header/Rejected"> REJECTED
        </Link>
        </li>
      </ul>
      </div>
      <Outlet/>
    </div>
  )
}

export default App_Header
