import React from 'react'
import Table from 'react-bootstrap/esm/Table'
import MoreVertIcon from '@mui/icons-material/MoreVert';
import { Fab } from '@mui/material';


function Lead_Disbursal() {
  
  let mystyle={
    table:{
      tableLayout:"fixed",
      width:"90%",
      // marginBottom:"110px",
      overflow: "auto",
      border:"2px solid grey",
      // border:"2px solid black"
  },
    th:{
      width:"310px",
      maxWidth:"300px",
      minWidth:"100px",
    }

  }

  return (
    <div className='container-lg'>
      <Table className='table-responsive shadow-sm p-3 mt-3  mb-4  bg-body rounded    ' striped bordered  hover  style={mystyle.table} >
          <thead  >
            <tr style={{backgroundColor:"rgb(210 217 222 / 22%)",}}>
              <th style={mystyle.th} >
              Loan Request Num
              </th>
              <th style={mystyle.th} >
              Proposed Loan Amt
              </th>
              <th style={mystyle.th}>
              Branch Name
              </th>
              <th style={mystyle.th}>
              Assigned To
              </th>
              <th style={mystyle.th}>
              Created Date
              </th>
              
              
            </tr>
            <tr>
              <td>
              JRUXQ3
              </td>
              <td>
              One Lakh
              </td>
              <td>
              ADHYAPAK NAGAR
              </td>
              <td>
              maker
              </td>
              
              <td>
              27 JUN 2022
              </td>
             
              
             
            </tr>
           
          </thead>
          <ul className="mt-5" style={{float:"right"}}>
            <li style={{display:"inline",margin:"10px"}}>
              FIRST
            </li>
            <li style={{display:"inline",margin:"10px"}}>
              PREVIOUS
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              <Fab  aria-label="add" style={{width:"40px",height:"40px",backgroundColor:"#17a2b8",border:"no border"}}>
                1
              </Fab>
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              NEXT
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              LAST
              </li>
          </ul>
        </Table>
      
    </div>
  )
}

export default Lead_Disbursal
