import React from 'react';
import Table from "react-bootstrap/esm/Table";
import Popover from "react-bootstrap/Popover";
import DeleteIcon from "@mui/icons-material/Delete";
import Button from "react-bootstrap/esm/Button";
import CloseIcon from '@mui/icons-material/Close';
// import './LoanDetails.css';

function LoanDetails_submitted({setOpenLoginDetails}) {
    let mystyle = {
        table: {
          tableLayout: "fixed",
          width: "100%",
          marginBottom: "110px",
          overflow: "auto",
          border: "2px solid grey",
        },
      };
      return (
        <div>
          <Popover
            id="popover-basic"
            style={{ maxWidth: 2000, margin: "100px",  }}
          >
            <div className="container">
              <div className="container-lg ">
              <CloseIcon className='icon'  onClick={()=>setOpenLoginDetails(false)} style={{float:"right"}}/>
                <ul
                  className="nav"
                  style={{
                    fontWeight: "5px",
                    display: "flex",
                    flexDirection: "row",
                    overflow: "auto",
                  }}
                >
                  <li className="nav-item m-3  ml-0">
                    <p>LOAN DETAILS</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p className="fs-3">APPLICANT</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p>REVIEW</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p>BANK DETAILS</p>
                  </li>
                  <li className="nav-item m-3 ">
                    <p>NOMINEE DETAILS</p>
                  </li>
                  <li className="nav-item m-3 w-4">
                    <p className="fs-6">DOCUMENT</p>
                  </li>
                  <li className="nav-item m-3 w-4">
                    <p className="fs-6">SANCTION</p>
                  </li>
                  <li className="nav-item m-3 w-4">
                    <p className="fs-6">ESIGN</p>
                  </li>
                </ul>
                <div className="container-lg">
                  <h2 className="p-3" style={{ borderBottom: "1px solid black" }}>
                    Loan Details
                  </h2>
                  <div className="container-lg">
                    <ul
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <li className="m-3">
                        <p className="mb-0">Gross Weight</p>
                        <input type="number" name="Gross Weight" id="" />
                      </li>
                      <li classname="m-3">
                        <p className="mb-0">Deduction</p>
                        <input type="number" name="Gross Weight" id="" />
                      </li>
                      <li className="m-3">
                        <p className="mb-0">Total net Weight(gm)</p>
                        <input type="number" name="Gross Weight" id="" />
                      </li>
                      <li className="m-3">
                        <p className="mb-0">Total Count</p>
                        <input type="number" name="Gross Weight" id="" />
                      </li>
                      <li className="m-3">
                        <p className="mb-0">Total Tare Weight</p>
                        <input type="number" name="Gross Weight" id="" />
                      </li>
                    </ul>
                    <Table
                      className="table-responsive shadow-sm p-3 mt-2 mb-4  bg-light rounded "
                      striped
                      bordered
                      hover
                      style={mystyle.table}
                    >
                      <thead>
                        <tr style={{ backgroundColor: "rgb(210 217 222 / 22%)" }}>
                          <th style={{ width: "0%" }}>Particulars</th>
                          <th style={{ width: "10%" }}>Finesse code</th>
                          <th style={{ width: "10%" }}>Gold Rate(per gm)</th>
                          <th style={{ width: "10%" }}>Gross Weight(gm)</th>
                          <th style={{ width: "100px" }}>Deduction</th>
                          <th style={{ width: "50px" }}>Net Weight</th>
                          <th style={{ width: "50px" }}>Count</th>
                          <th style={{ width: "50px" }}>Packet No.</th>
                          <th style={{ width: "5%" }}></th>
                        </tr>
                        <tr>
                          <td>
                            RING
                            </td>
                        <td>
                          20k    
                          </td>
                          <td>3820.81</td>
                          <td>
                            10
                          </td>
    
                          <td>
                            1
                          </td>
                          <td>9</td>
                          <td>
                            1
                          </td>
                          <td>
                            934
                          </td>
                          <td>
                            
                          </td>
                        </tr>
                      </thead>
                    </Table>
                    
                    <ul
                      style={{
                        display: "flex",
                        justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <li className="m-3 ml-0 " style={{ width: "100px" }}>
                        <p className="mb-0">Ornament Value</p>
                        <input
                          style={{ width: "130px" }}
                          type="number"
                          name="Gross Weight"
                          id=""
                        />
                      </li>
                      <li classname="m-3" style={{ width: "130px" }}>
                        <p className="mb-0">Actual Loan Amount</p>
                        <input
                          style={{ width: "130px" }}
                          type="number"
                          name="Gross Weight"
                          id=""
                        />
                      </li>
                      <li className="m-3" style={{ width: "100px" }}>
                        <p className="mb-0">Loan Scheme</p>
                        <input
                          style={{ width: "130px" }}
                          type="number"
                          name="Gross Weight"
                          id=""
                        />
                      </li>
                      <li classname="m-3" style={{ width: "100px" }}>
                        <p className="mb-0">Monthly Interest</p>
                        <input
                          style={{ width: "130px" }}
                          type="number"
                          name="Gross Weight"
                          id=""
                        />
                      </li>
                      <li className="m-3" style={{ width: "130px" }}>
                        <p className="mb-0">Tenure (in Months)*</p>
                        <input
                          style={{ width: "130px" }}
                          type="number"
                          name="Gross Weight"
                          id=""
                        />
                      </li>
                      <li className="m-3" style={{ width: "100px" }}>
                        <p className="mb-0">LTV</p>
                        <input
                          style={{ width: "130px" }}
                          type="number"
                          name="Gross Weight"
                          id=""
                        />
                      </li>
                    </ul>
    
                    <ul
                      style={{
                        display: "flex",
                        // justifyContent: "space-between",
                        alignItems: "center",
                      }}
                    >
                      <li className="ml-5">
                        <p className="mb-0">PAN NO.</p>
                        <input type="text" />
                      </li>
                      
                      <li className="ml-5">
                        <p className="mb-0"> APPLICANT NAME</p>
                        <input type="text" />
                      </li>
                    </ul>
                    <div className="container-lg">
                      <Table no striped bordered hover>
                        <thead>
                          <tr>
                            <th>Days</th>
                            <th>Interest</th>
                          </tr>
                          <tr>
                            <td>0-30</td>
                            <td>1% PM (12% PA)</td>
                          </tr>
                          <tr>
                            <td>31-60</td>
                            <td>1.25% PM (15% PA)</td>
                          </tr>
                          <tr>
                            <td>61-90</td>
                            <td>1.5% PM (18% PA)</td>
                          </tr>
                          <tr>
                            <td>91-120</td>
                            <td>2% PM (24% PA)</td>
                          </tr>
                        </thead>
                      </Table>
                      <div
                        className="container-lg mb-3"
                        style={{
                          display: "inline-flex",
                          flexDirection: " row-reverse",
                        }}
                      >
                        <Button
                          className="btn shadow p-2 mb-3 ml-3 bg-info  rounded"
                          variant="primary"
                          type="submit"
                          style={{ width: "100px", border: "1px solid grey" }}
                        >
                          Next
                        </Button>
                        <Button
                          className="btn shadow p-2 mb-3 ml-3 bg-secondary  rounded"
                          variant="primary"
                          onClick={()=>setOpenLoginDetails(false)}
                          type="close"
                          style={{ width: "100px", border: "1px solid grey" }}
                        >
                          Back
                        </Button>
                      </div>
                    </div>
                  </div>
                </div>
    
                {/* <button onClick={() => setOpenLoginDetails(false)}>close</button> */}
              </div>
            </div>
          </Popover>
        </div>
      );
}

export default LoanDetails_submitted
