import React,{useState} from 'react'
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/esm/Button';
import Row from 'react-bootstrap/esm/Row';
import Col from 'react-bootstrap/esm/Col';
import Popover from 'react-bootstrap/Popover';
import { useNavigate } from 'react-router-dom';
import { Modal } from 'bootstrap';
// import { Popover } from 'bootstrap';

function Create_Application({setShow}) {
    
    
    
    let navigate = useNavigate();
    const handleOnSubmit=()=>{
       console.log("Submitted");
       
    }
    const handleOnClose=()=>{
      console.log("clicked");
      setShow(false);
    }
    let mystyle={
        body:{
            // fontFamily:"'Times New Roman', Times, serif",
        }
    }
  return (
    <div style={mystyle.body}>
      {/* <h1>this is my modal</h1> */}

        <Popover id="popover-basic" style={{ maxWidth: 2000,margin:"100px",width:"70vw", }} >
      <div className="container p-5 m-0 mb-0 " style={{height:"60vh"}} >
        <h2 style={{borderBottom:"1px solid black"}}>
            Create Application
        </h2>
        <Row>

        <Col>
        




        <Form.Group className="mb-3">
            
        <Form.Label >Name</Form.Label>
        <Form.Control placeholder="Name" style={{width:"70%",height:"30px",border:"1px solid grey"}} />
      </Form.Group>
      </Col>
        <Col >
      <Form.Group className="mb-3">
        <Form.Label >Mobile No.</Form.Label>
        <Form.Control placeholder="999*******" style={{width:"70%",height:"30px",border:"1px solid grey"}} />
        
      </Form.Group>
      </Col>
      </Row>
      <Form.Group className="mb-3">
        <Form.Label>Select Loan Amount</Form.Label>
        <Form.Select style={{width:"50%",height:"30px",borderRadius:"5px",marginLeft:"10px",border:"no border"}} >
          <option>Less Than 1 Lakh</option>
          <option>Between 1-5 Lakhs</option>
          <option>Above 5 Lakhs</option>
          <option></option>
        </Form.Select>
      </Form.Group>
      <Form.Group className="mb-3">
        <Form.Check type="checkbox" label="Correct Information" />
      </Form.Group>
      <div style={{float:"right"}}>
      <Button className='btn shadow p-2 mb-5 ml-3 bg-info  rounded' variant="primary" type="submit" style={{width:"100px",border:"1px solid grey"}} onSubmit={handleOnSubmit}>Submit</Button>
      <Button className='btn shadow p-2 mb-5 ml-3 bg-info  rounded' variant="primary" type="close" style={{width:"100px" ,border:"1px solid grey"}} onClick = {handleOnClose}>Close</Button>
      </div>
     
      </div>
      </Popover>
    </div>
  )
}

export default Create_Application
