import React from 'react'
import Table from 'react-bootstrap/esm/Table'
import { Fab } from '@mui/material';
import MoreVertIcon from '@mui/icons-material/MoreVert';


function Rejected() {
    let mystyle={
        body:{
            // fontFamily:"'Times New Roman', Times, serif",
        },
        head:{
            borderBottom: "1px solid black"
        },
        table:{
            tableLayout:"fixed",
            width:"100%",
            marginBottom:"110px",
            overflow: "auto",
            border:"2px solid grey",
            // border:"2px solid black"
            
        }
    }
  return (
    <>
      <div className='container-lg shadow p-2  mb-5 ' style={mystyle.body}>
        <div  style={mystyle.head}>
        <h2>Rejected </h2>
        </div>
        <Table className='table-responsive shadow-sm p-3 mt-5 mb-4  bg-body rounded    ' striped bordered  hover  style={mystyle.table} >
          <thead  >
            <tr style={{backgroundColor:"rgb(210 217 222 / 22%)",}}>
              <th style={{width:"9%"}}>
              Application ID
              </th>
              <th style={{width:"13%"}}>
              Application Form Number
              </th>
              <th>
              Loan Account Number
              </th>
              <th>
              Customer ID
              </th>
              <th>
              Customer Name
              </th>
              
              <th style={{width:"9%"}}>
              Product Type
              </th>
              <th style={{width:"7%"}}>
              Action
              </th>
            </tr>
            
          </thead>
          
        </Table>
      

      </div>
    </>
  )
}

export default Rejected
