import React,{useState} from 'react'
import Button from 'react-bootstrap/esm/Button'
import Table from 'react-bootstrap/esm/Table'
import MoreVertIcon from '@mui/icons-material/MoreVert';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Create_Application from './Create_Application';
import { Fab } from '@mui/material';
import './Inprocess.css'
import Login_Details from './Loan_Details_header';

function In_Process() {
 
  // const [buttonPopUp,setButtonPopUp] = useState(false);
  const [show, setShow] = useState(false);
  const [openLoginDetails,setOpenLoginDetails] = useState(false);

  const handleVertIcon=()=>{
    console.log("clicked");
    setOpenLoginDetails(true);

  }

  const handleOnClick=()=>{
    setShow(true);
    console.log("clicked");
    // setButtonPopUp(true);
    console.log("clicked");
  }
  let mystyle={
    table:{
      tableLayout:"fixed",
      width:"100%",
      marginBottom:"110px",
      overflow: "auto",
      border:"2px solid grey",
  },
  icon:{
    "&:hover":{
      background: "#efefef",
    },

     },
  
  }
  return (
    <div >
        <div className="container-lg shadow p-2  mb-5 " >
        <div style={{borderBottom:"1px solid black",display:"flex",flexDirection:"row",justifyContent:"space-between"}}>
        <h2 >In Process</h2>

       
        <Button className='btn shadow p-2 mb-3 mr-2 bg-info  rounded ' variant="primary" size="md" onClick={handleOnClick}   >
          CREATE APPLICATION
        </Button>
        {show && <Create_Application setShow={setShow}/>}
       
        
        
        </div>

        <Table className='table-responsive shadow-sm p-3 mt-5 mb-4  bg-body rounded ' striped bordered hover style={mystyle.table}>
          <thead >
            <tr style={{backgroundColor:"rgb(210 217 222 / 22%)"}}>
              <th style={{width:"9%"}}>
              Application ID
              </th>
              <th style={{width:"13%"}}>
              Application Form Number
              </th>
              <th>
              Loan Account Number
              </th>
              <th>
              Customer ID
              </th>
              <th>
              Customer Name
              </th>
              <th>
              Mobile Number
              </th>
              <th style={{width:"9%"}}>
              Product Type
              </th>
              <th style={{width:"7%"}}>
              Action
              </th>
            </tr>
            <tr>
              <td>
                7004
              </td>
              <td>
              APPL0000000224
              </td>
              <td>
              IFLSMC0000000230	
              </td>
              <td>
              CUST0000000197
              </td>
              
              <td>
             ANKIT
              </td>
              <td>
              9099757000
              </td>
              <td>
              Gold Loan
              </td>
              <td >
                {/* <button> */}
                <MoreVertIcon className="icon" onClick={handleVertIcon}/>
                {openLoginDetails && <Login_Details setOpenLoginDetails={setOpenLoginDetails}/>}
                {/* </button> */}
              
              </td>
            </tr>
          </thead>
          <div>
          <ul className="mt-5" style={{float:"right"}}>
            <li style={{display:"inline",margin:"10px"}}>
              FIRST
            </li>
            <li style={{display:"inline",margin:"10px"}}>
              PREVIOUS
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              <Fab  aria-label="add" style={{width:"40px",height:"40px",backgroundColor:"#17a2b8",border:"no border"}}>
                1
              </Fab>
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              NEXT
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              LAST
              </li>
          </ul>
          </div>
         
        </Table>
        
        </div>
        

        </div>
      
    // </div>
  )
}

export default In_Process
