import React from 'react'
import ifl from './ifl_lms/ifl.png';
import './Login.css'
import PersonIcon from '@mui/icons-material/Person';


function Forget_password() {
    let mystyle={
      body:{
        height:"100vh"
      },
        container: {
            width: "40vw",
            border: "1px solid grey",
            marginTop: "90px",
            
            
          },
          image: {
            height: "70px",
            width: "150px",
            marginTop: "20px",
          },
          input:{
          maxWidth: "30vw",
          minWidth: "50px",
          },
          Button:{
           width: "100px",
           border: "1px solid black"
          }
          
             
    }
  return (
    <div style={mystyle.body}>
        <div className="container" style={mystyle.container}>
        <div className="text-center">
          <img src={ifl} style={mystyle.image} alt="" />
          <hr style={mystyle.hr} />
        </div>
        <h2 className='m-3'>Reset Your Password</h2>
        <h5 className="mt-4 mx-4 mb-1"><PersonIcon/> User Name</h5>
        <input className='mt-1 mx-4' style={mystyle.input} type="text" name="forget" id="" />
        <div className="text-left ml-4 mt-4 mb-3">
            <button type="button" className="btn btn-success   " style={mystyle.Button}>Submit</button> 
        </div>
        </div>
    </div>
  )
}

export default Forget_password
