import React,{useState} from 'react'
import Table from 'react-bootstrap/esm/Table'
import { Fab } from '@mui/material';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import Completed_loanDetails from './Completed_loanDetails';
import Second_LoanDetails from './Second_LoanDetails'
import './Completed.css'

function Completed() {

  const [openLoanDetails,setOpenLoanDetails] = useState(false);
  const [LoanDetails,setLoanDetails] = useState(false);
  
  const handleVertIcon=()=>{
    console.log("clicked");
    setOpenLoanDetails(true);

  } 
  const handleVertIcon2=()=>{
    console.log("clicked");
    setLoanDetails(true);

  } 
    let mystyle={
        body:{
            fontFamily:"'Times New Roman', Times, serif",
        },
        head:{
            borderBottom: "1px solid black"
        },
        table:{
            tableLayout:"fixed",
            width:"100%",
            marginBottom:"110px",
            overflow: "auto",
            border:"2px solid grey",
            // border:"2px solid black"
            
        }
    }
  return (
    <>
      <div className='container-lg shadow p-2  mb-5 ' style={mystyle.body}>
        <div  style={mystyle.head}>
        <h2>Completed Details</h2>
        </div>
        <Table className='table-responsive shadow-sm p-3 mt-5 mb-4  bg-body rounded    ' striped bordered  hover  style={mystyle.table} >
          <thead  >
            <tr style={{backgroundColor:"rgb(210 217 222 / 22%)",}}>
              <th style={{width:"9%"}}>
              Application ID
              </th>
              <th style={{width:"13%"}}>
              Application Form Number
              </th>
              <th>
              Loan Account Number
              </th>
              <th>
              Customer ID
              </th>
              <th>
              Customer Name
              </th>
              
              <th style={{width:"9%"}}>
              Product Type
              </th>
              <th style={{width:"7%"}}>
              Action
              </th>
            </tr>
            <tr>
              <td>
                6997
              </td>
              <td>
              APPL0000000218
              </td>
              <td>
              ADHSMC0000000224
              </td>
              <td>
              CUST0000000195
              </td>
              
              <td>
              ANKIT BANSAL
              </td>
             
              <td>
              Gold Loan
              </td>
              <td >
              <MoreVertIcon className="icon" onClick={handleVertIcon}/>
              {openLoanDetails && <Completed_loanDetails setOpenLoanDetails={setOpenLoanDetails}/>}
              
              </td>
            </tr>
            <tr>
              <td>
                6989
              </td>
              <td>
              APPL0000000212
              </td>
              <td>
              ADH120000000220
              </td>
              <td>
              CUST0000000137
              </td>
              
              <td>
              RAM
              </td>
             
              <td>
              Gold Loan
              </td>
              <td >
              <MoreVertIcon className="icon" onClick={handleVertIcon2}/>
              {LoanDetails && <Second_LoanDetails setLoanDetails={setLoanDetails}/>}
              
              </td>
            </tr>
          </thead>
          <ul className="mt-5" style={{float:"right"}}>
            <li style={{display:"inline",margin:"10px"}}>
              FIRST
            </li>
            <li style={{display:"inline",margin:"10px"}}>
              PREVIOUS
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              <Fab  aria-label="add" style={{width:"40px",height:"40px",backgroundColor:"#17a2b8",border:"no border"}}>
                1
              </Fab>
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              NEXT
              </li>
              <li style={{display:"inline",margin:"10px"}}>
              LAST
              </li>
          </ul>
        </Table>
      

      </div>
    </>
  )
}

export default Completed
