import React from 'react'
import Row from 'react-bootstrap/esm/Row';
import Col from 'react-bootstrap/esm/Col';


function Gold_rate() {
  return (
    <div>
      <div className="container-lg ml-5 mb-4">
       <Row>
        <Col>
        <div>
         <h6> Date From</h6>
         <input style={{borderRadius:"5px",width:"40%",backgroundColor:"rgb(230, 242, 252)"}} type="date" name="date" id="" />
         
         </div>
         </Col>
         <Col>
      <div >
        <h6>Date To</h6>
        <input style={{borderRadius:"5px",width:"40%",backgroundColor:"rgb(230, 242, 252)"}} type="date" name="date" id="" />
      </div>
      </Col>
      </Row>
      </div>
      
    </div>
  )
}

export default Gold_rate
