import React from 'react'
import Table from 'react-bootstrap/esm/Table'

function Lead_Rejected() {
  let mystyle={
    table:{
      tableLayout:"fixed",
      width:"90%",
      // marginBottom:"110px",
      overflow: "auto",
      border:"2px solid grey",
      // border:"2px solid black"
  },
    th:{
      width:"310px",
      maxWidth:"300px",
      minWidth:"100px",
    }

  }
  return (
    <div>
      <Table className='table-responsive shadow-sm p-3 mt-3  mb-4  bg-body rounded    ' striped bordered  hover  style={mystyle.table} >
          <thead  >
            <tr style={{backgroundColor:"rgb(210 217 222 / 22%)",}}>
              <th style={mystyle.th} >
              Loan Request Num
              </th>
              <th style={mystyle.th} >
              Proposed Loan Amt
              </th>
              <th style={mystyle.th}>
              Branch Name
              </th>
              <th>
                Reason
              </th>
              <th style={mystyle.th}>
              Additional Notes
              </th>
              <th style={mystyle.th}>
              Created Date
              </th>
              
              
            </tr>
           
          </thead>
         
        </Table>
      
    </div>
  )
}

export default Lead_Rejected
